.footer_outer_container {
  background-color: #111;
  color: grey;
  display: flex;
  justify-content: center;
  padding: 50px 0;
}

.footer_inner_container {
  width: 70%;
  max-width: 1000px;
}

.footer_icons {
  width: 150px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.footer_icons svg {
  cursor: pointer;
  transition: transform 0.2s;
}

.footer_icons svg:hover {
  transform: scale(1.2);
  color: #e50914;
}

.footer_data {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}

.footer_data ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer_data ul li {
  cursor: pointer;
  margin: 15px 0;
  transition: color 0.2s;
}

.footer_data ul li:hover {
  color: #fff;
}

.service_code {
  border: 1px solid grey;
  padding: 8px 12px;
  display: inline-block;
  margin: 20px 0;
  cursor: pointer;
  transition: all 0.2s;
}

.service_code:hover {
  color: #fff;
  border-color: #fff;
}

.copy-write {
  font-size: 12px;
  margin-top: 20px;
}
